import { ChangeEvent, Fragment, useCallback, useEffect, useState } from "react";
import { Helmet } from "react-helmet-async";
import { Link } from "react-router-dom";
import { Card, CardContent, Divider, IconButton } from "@mui/material";
import { Clear } from "@mui/icons-material";

import WATemplatesApi from "Apis/WATemplatesApi";
import { useWaTemplateApi, useWaTemplateData } from "Contexts/WhatsApp";
import { useGlobal } from "Contexts/App";
import { useSnacks } from "Contexts/Snack";
import {
  ActionButton,
  CardSX,
  CardContentSX,
  Content,
  ContentInner,
  InnerWrapper,
  ContentHeader,
  HeaderTitle,
  InnerBlock,
  Cta,
  CtaInfo,
  CtaInfoText,
  SearchWrapper,
  SearchBlock,
  SearchInput,
  InnerWrapperSX,
  ContentFooter,
  ContentInnerSX,
  ContentHeaderSX,
} from "Containers/BusinessSettings/LayoutComponents";
import { LeftArrow, Search } from "Assets/Icons";
import WTMItem from "./WATemplateMessages/WTMItem";
import WTMHeader from "./WATemplateMessages/WTMHeader";
import WaTemplateForm from "./WATemplateMessages/WaTemplateForm";
import WaTemplatePreview from "./WATemplateMessages/WaTemplatePreview";

const WATemplateMessages = () => {
  const { isMobile, isTablet } = useGlobal();
  const { setErrorMessage } = useSnacks();
  const [loading, setLoading] = useState<boolean>(true);
  const [isSearchActive, setSearchActive] = useState<boolean>(false);
  const {
    waTemplatesData,
    waTemplateFilter,
    showWaTemplateForm,
    showWaPreviewForm,
    showWaTemplatePreview,
  } = useWaTemplateData();
  const {
    setWaTemplatesData,
    setTotalWaTemplates,
    setWaTemplateFilter,
    setShowWaTemplateForm,
    setShowWaPreviewForm,
    setShowWaTemplatePreview,
    setEditWaTemplateId,
    setPreviewWaTemplateId,
  } = useWaTemplateApi();
  const fetchWaTemplateMessages = useCallback(async () => {
    try {
      const res = await WATemplatesApi.getWaTemplateMessages();
      if (!res.success) {
        throw Error(res.message);
      }
      setTotalWaTemplates(res.payload.length);
      setWaTemplatesData(res.payload);
      setLoading(false);
    } catch (error: any) {
      setErrorMessage(error.message);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    fetchWaTemplateMessages();
  }, [fetchWaTemplateMessages]);

  return (
    <Fragment>
      <Helmet>
        <title>WhatsApp Template Messages</title>
      </Helmet>
      <Content>
        {isMobile && (
          <ContentHeader sx={ContentHeaderSX}>
            {!isSearchActive && (
              <IconButton
                component={Link}
                to="/business-settings"
                sx={{ width: 32, height: 32 }}
              >
                <LeftArrow />
              </IconButton>
            )}
            {!isSearchActive && (
              <HeaderTitle>{`Template Messages`}</HeaderTitle>
            )}
            {isSearchActive && (
              <SearchWrapper>
                <SearchBlock>
                  <IconButton
                    disableTouchRipple
                    disableFocusRipple
                    disableRipple
                    sx={{ position: "absolute" }}
                  >
                    <Search />
                  </IconButton>
                  <SearchInput
                    onChange={({
                      target: { value },
                    }: ChangeEvent<HTMLInputElement>) =>
                      setWaTemplateFilter(value)
                    }
                    placeholder="Search Template"
                  />
                  <IconButton
                    onClick={() => {
                      setWaTemplateFilter("");
                      setSearchActive(!isSearchActive);
                    }}
                    sx={{ position: "absolute", right: 0, top: 0 }}
                  >
                    <Clear />
                  </IconButton>
                </SearchBlock>
              </SearchWrapper>
            )}
            {!isSearchActive && waTemplatesData.length > 0 && (
              <IconButton
                onClick={() => setSearchActive((prevState) => !prevState)}
              >
                <Search />
              </IconButton>
            )}
          </ContentHeader>
        )}
        <ContentInner sx={ContentInnerSX}>
          <InnerWrapper sx={InnerWrapperSX}>
            {!loading && (
              <Card elevation={0} sx={CardSX}>
                <CardContent sx={CardContentSX}>
                  {waTemplatesData.length === 0 ? (
                    <InnerBlock>
                      <Cta>
                        <ActionButton
                          disableIcon
                          onClick={() => setShowWaTemplateForm(true)}
                          sx={{
                            borderRadius: 2,
                            boxShadow: "0px 8px 8px rgba(50, 197, 255, 0.1)",
                            fontSize: 16,
                            fontWeight: 700,
                            height: 36,
                            lineHeight: "19px",
                            margin: "auto",
                            textTransform: "capitalize",
                          }}
                        >
                          {"New Template Message"}
                        </ActionButton>
                      </Cta>
                      <CtaInfo>
                        <CtaInfoText>
                          {
                            "In order to initiate a conversation message your customers. Create a new template message. Send a template message to masses from "
                          }
                          <strong>{"Broadcast"}</strong>
                          {" option in "}
                          <strong>{"Engage"}</strong>
                          {" workspace."}
                        </CtaInfoText>
                      </CtaInfo>
                    </InnerBlock>
                  ) : (
                    <Fragment>
                      {isTablet && (
                        <WTMHeader {...{ isSearchActive, setSearchActive }} />
                      )}
                      {isTablet && <Divider />}
                      {waTemplatesData
                        .filter((waTemplate) =>
                          waTemplate?.name
                            ?.toLowerCase()
                            .includes(waTemplateFilter?.toLowerCase())
                        )
                        .map((waTemplate, i: number) => (
                          <WTMItem key={i} {...waTemplate} />
                        ))}
                    </Fragment>
                  )}
                </CardContent>
              </Card>
            )}
          </InnerWrapper>
        </ContentInner>
        {isMobile && waTemplatesData.length > 0 && (
          <ContentFooter>
            <ActionButton
              onClick={() => setShowWaTemplateForm(true)}
              variant="outlined"
              color="primary"
              disableIcon
            >
              New Template Message
            </ActionButton>
          </ContentFooter>
        )}
      </Content>
      <WaTemplateForm
        open={showWaTemplateForm}
        closeDrawer={() => {
          setEditWaTemplateId(undefined);
          setShowWaTemplateForm(false);
        }}
      />
      <WaTemplatePreview
        open={showWaPreviewForm || showWaTemplatePreview}
        closeDrawer={() => {
          setPreviewWaTemplateId(undefined);
          setShowWaPreviewForm(false);
          setShowWaTemplatePreview(false);
        }}
      />
    </Fragment>
  );
};

export default WATemplateMessages;
